/*
 * Arrows
 */
.MagicScroll-horizontal .mcs-button-arrow-prev:before,
.MagicScroll-horizontal .mcs-button-arrow-next:before,
.MagicScroll-vertical .mcs-button-arrow-prev:before,
.MagicScroll-vertical .mcs-button-arrow-next:before {
  content: "";
  top: 50%;
  left: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  border-width: 3px;
  border-color: rgba(49, 51, 61, 0.6);
  border-style: solid;
  border-right: none;
  border-bottom: none;
  -webkit-transition: border-color 0.15s linear;
  transition: border-color 0.15s linear;
}

.MagicScroll-horizontal .mcs-button-arrow-prev:before {
  left: 62%;
  -webkit-transform: translate(-50%, -50%) rotateZ(-45deg);
  transform: translate(-50%, -50%) rotateZ(-45deg);
}

.MagicScroll-horizontal .mcs-button-arrow-next:before {
  left: 40%;
  -webkit-transform: translate(-50%, -50%) rotateZ(135deg);
  transform: translate(-50%, -50%) rotateZ(135deg);
}
.MagicScroll-vertical .mcs-button-arrow-prev:before {
  top: 62%;
  -webkit-transform: translate(-50%, -50%) rotateZ(45deg);
  transform: translate(-50%, -50%) rotateZ(45deg);
}
.MagicScroll-vertical .mcs-button-arrow-next:before {
  top: 40%;
  -webkit-transform: translate(-50%, -50%) rotateZ(-135deg);
  transform: translate(-50%, -50%) rotateZ(-135deg);
}
.mcs-button-arrow.mcs-disabled {
  opacity: 0.01 !important;
}
.MagicScroll > .mcs-button-arrow {
  /*font-size: 20px;*/
  width: 30px;
  height: 30px;
  margin: auto;
  opacity: 0.6;
  -webkit-transition: opacity 0.15s linear;
  transition: opacity 0.15s linear;
}
.MagicScroll-horizontal .mcs-button-arrow-prev {
  left: 0;
  right: auto;
  top: 0;
  bottom: 0;
}
.MagicScroll-horizontal .mcs-button-arrow-next {
  left: auto;
  right: 0;
  top: 0;
  bottom: 0;
}
.MagicScroll-vertical .mcs-button-arrow-prev {
  top: 0;
  bottom: auto;
  left: 0;
  right: 0;
}
.MagicScroll-vertical .mcs-button-arrow-next {
  top: auto;
  bottom: 0;
  left: 0;
  right: 0;
}

.MagicScroll:hover > .mcs-button-arrow:hover:not(.mcs-disabled) {
  opacity: 1;
}

.MagicScroll:hover > .mcs-button-arrow:hover:not(.mcs-disabled):before {
  border-color: rgba(49, 51, 61, 1);
}

.MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow {
  width: 24px;
  height: 24px;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 1px rgba(153, 153, 153, 0.5);
  -webkit-transition: background-color 0.15s linear;
  transition: background-color 0.15s linear;
}

.MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow:before {
  width: 8px;
  height: 8px;
}

.MagicScroll-horizontal.MagicScroll-arrows-inside:hover
  > .mcs-button-arrow:hover:not(.mcs-disabled) {
  background-color: rgba(255, 255, 255, 0.96);
}
.MagicScroll-horizontal.MagicScroll-arrows-inside .mcs-button-arrow-prev {
  left: 5px;
}
.MagicScroll-horizontal.MagicScroll-arrows-inside .mcs-button-arrow-next {
  right: 5px;
}
.MagicScroll-vertical.MagicScroll-arrows-inside .mcs-button-arrow-prev {
  top: 5px;
}
.MagicScroll-vertical.MagicScroll-arrows-inside .mcs-button-arrow-next {
  bottom: 5px;
}

.lt-ie10-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow {
  border: 1px solid rgba(153, 153, 153, 0.5);
}
.lt-ie9-magic .mcs-button-arrow:before {
  border-color: #83858b;
}
.lt-ie9-magic .MagicScroll:hover > .mcs-button-arrow:hover:before {
  border-color: #31333d;
}
.lt-ie9-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow:before {
