.rangeright {
  -webkit-appearance: none;
  background: var(--mainclr);
  width: 100%;
  height: 0.5vh;
  opacity: 2;
  border-bottom: 0.1vh solid var(--mainshwclr);
  /* border-bottom: 0.4vh solid rgb(166, 130, 74); */
  border-radius: 0.5vh;
}
.rangeright::-moz-range-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background: var(--mainclr);
  width: 3.5vh;
  height: 1.5vh;
  border-radius: 0.3vh;
  border-bottom: 0.2vh solid rgb(166, 130, 74);
}
.rangeright::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background: var(--mainclr);
  width: 3.5vh;
  height: 1.6vh;
  border-radius: 0.3vh;
  border-bottom: 0.2vh solid rgb(166, 130, 74);
}

.rangeleft {
  -webkit-appearance: none;
  background: var(--mainclr);
  width: 100%;
  height: 0.8vh;
  opacity: 2;
  border-radius: 0.5vh;
}

.rangeleft::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background: var(--mainclr);
  width: 5vh;
  height: 2vh;
  border-radius: 0.5vh;
  border-bottom: 0.5vh solid rgb(166, 130, 74);
}
