.trapezium {
  flex: 1;
  height: 2vh;
  transform: skew(-45deg);
  background-color: var(--mainclr);
  margin: 0px 0.5vh;
  border: 0.1vh solid var(--mainclr);
  border-bottom: 0.5vh solid var(--mainshwclr);
}

.bonusbar1 {
  flex: 1;
  height: 0.8vh;
  transform: skew(-45deg);
  background-color: var(--mainclr);
  margin: 0px 0.2vh;
  border-bottom: 0.5vh solid var(--mainshwclr);
}

.bonusbar2 {
  flex: 1;
  height: 0.8vh;
  border-radius: 3vh;
  background-color: var(--mainclr);
  margin: 0px 0.2vh;
  border-bottom: 0.5vh solid var(--mainshwclr);
}

.empty {
  background-color: transparent;
}
