  --clr125: rgb(189, 41, 41);
  --clrbrag: rgb(201, 91, 63);
  --clradmin: rgb(255, 252, 18);
  --clrmod: rgb(41, 189, 105);
  --winblkclr: rgb(44, 49, 55);
  --wineachclr: rgb(23, 28, 34);
  --logbackclr: rgb(44, 49, 55);
  --logmodalbackclr: rgb(25, 30, 36);
}

.uppercase {
  text-transform: uppercase;
}

.textalign {
