.winnermainheader {
  height: 5.4vh;
  background-color: rgb(44, 49, 55);
  margin: 0.3vh;
  display: flex;
  justify-content: center;
  font-size: 0.9vw;
  align-items: center;
  border-radius: 0.2vw;
}
.chatactchatbtn {
  color: var(--black);
  cursor: pointer;
  padding: 0.3vh 0.5vh;
  font-size: 1vh;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 20%;
  background-color: var(--mainclr);
  border-bottom: 0.5vh solid var(--mainshwclr);
}
.chatactico {
  flex: 1.5 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.chatcon {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 0.6vw;
}
.chatcircle {
  width: 0.6vw;
  height: 0.6vw;
  border-radius: 50%;
  background-color: var(--mainclr);
  margin-left: 0.25vw;
  margin-right: 0.25vw;
}
.chatconmain {
  height: 5vh;
  background-color: rgb(32, 37, 43);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.winnermain {
  width: 18.4%;
  display: flex;
  flex-direction: column;
  background-color: var(--backclr);
}
.winnermain * {
  font-family: "Montserrat";
}

.divedit:empty:not(:focus):before {
  content: attr(data-placeholder);
  color: rgb(172, 172, 172);
  font-size: 0.6vw;
}
.inputedit {
  max-height: 10vh;
  min-height: 2vh;
  height: 2vh;
  border-radius: 5px;
  background-color: rgb(44, 49, 55);
  border: 0px none red;
  font-size: 1.5vh;
  padding: 1.5vh 3.5vh 1.5vh 1.5vh;
  justify-content: center;
  width: 100%;
  display: flex;
  align-items: center;
  margin: 0px 1vh;
  box-sizing: none;
  resize: none;
}
.inputedit::-webkit-scrollbar {
  display: none;
}
.inputedit {
  scrollbar-width: none;
}
.inputedit:focus {
  outline: none;
}

.divedit {
  display: flex;
  align-items: center;
  width: 100%;
  height: 5vh;
  margin: 0px 1vh;
  padding: 0px 1vh;
  background-color: rgb(44, 49, 55);
  border-radius: 5px;
  font-size: 0.6vw;
}

.chatnotifymain {
  padding: 0.3vw 0.5vw 0.5vw 0.5vw;
  background-color: rgb(45, 50, 54);
  border-width: 0.25vw;
  border-color: var(--mainclr);
  border-style: none none none solid;
}

.chatroundnotify {
  padding: 0.3vw 0.5vw;
  background-color: rgb(45, 50, 54);
  border-width: 0.25vw;
  border-color: var(--mainclr);
  border-style: none none none solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.chatwonnotify {
  padding: 0.3vw 0.5vw;
  background-color: rgb(45, 50, 54);
  border-width: 0.25vw;
  border-color: var(--mainclr);
  border-style: none none none solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.chatmodchat {
  padding: 0.3vw 0.5vw;
  font-size: 0.55vw;
}
.chatmenuico:hover {
  opacity: 1 !important;
  z-index: 2000;
}
.chatmenuico {
  display: none;
  position: fixed;
  background-color: rgb(32, 37, 43);
  padding: 0.5vh;
  border-style: solid solid solid none;
  border-width: 0.3vh;
  border-color: var(--mainclr);
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: 0.5s all;
  transition: 0.5s all;
  top: 10vh;
  left: 0;
}
.SignDialogClosebtn {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  z-index: 3000;
}
.SignDialogClosebtn > i {
  font-size: 14px;
}
.SignDialogClosebtn > i:hover {
  font-size: 18px;
}
.chatmenuclsico {
  /* animation: 0.5s linear 0s chaticoslide; */
  z-index: 3000;
  position: absolute;
  background-color: rgb(32, 37, 4);
  padding: 0.5vh;
  border-style: solid solid solid none;
  border-width: 0.3vh;
  border-color: var(--mainclr);
  cursor: pointer;
  opacity: 1;
  top: 10vh;
  left: -30px;
  -webkit-transition: 1s all;
  transition: 1s all;
}
.SignDialog .MuiPaper-root {
  background-color: transparent !important;
}
.comchatmain {
  padding: 0.3vw 0.5vw;
  font-size: 0.55vw;
  overflow-wrap: break-word;
}
.chatadminchat {
  padding: 0.3vw 0.5vw;
  color: var(--clradmin);
  font-size: 0.55vw;
}

