.MagicScroll-vertical.MagicScroll-arrows-inside .mcs-button-arrow-next {
  bottom: 5px;
}

.lt-ie10-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow {
  border: 1px solid rgba(153, 153, 153, 0.5);
}
.lt-ie9-magic .mcs-button-arrow:before {
  border-color: #83858b;
}
.lt-ie9-magic .MagicScroll:hover > .mcs-button-arrow:hover:before {
  border-color: #31333d;
}
.lt-ie9-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow:before {
  border-color: #31333d;
}
.lt-ie9-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow {
  background: #fff;
  border: 1px solid #999;
  filter: alpha(opacity = 60);
}
.lt-ie9-magic .MagicScroll.MagicScroll-arrows-inside .mcs-button-arrow:hover {
  filter: alpha(opacity = 96);
}
.lt-ie9-magic .mcs-button-arrow.mcs-disabled {
  filter: alpha(opacity = 0.01) !important;
}

/*
 * Bullets
 */
.MagicScroll-horizontal .mcs-bullets {
  margin-top: 10px;
}
.MagicScroll-horizontal.MagicScroll-bullets {
  margin-bottom: 35px;
}
.MagicScroll-vertical .mcs-bullets {
  margin-left: 10px;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.MagicScroll-vertical.MagicScroll-bullets {
  margin-right: 35px;
}

.mcs-bullet {
  width: 10px;
  height: 10px;
  background-color: #83858b;
}
.mcs-bullet:hover {
  background-color: #5a5a5a;
}
.mcs-bullet.active {
  background-color: transparent;
  border: 1px solid #5a5a5a;
}

/* Caption */
.mcs-item .mcs-caption {
  color: white;
  padding: 5px 0;
  font: normal 9pt/1.2em "Helvetica Neue", Helvetica, "Lucida Grande",
    "Lucida Sans Unicode", Verdana, Arial, sans-serif;
  text-rendering: geometricPrecision;
}
.MagicScroll[data-mode="scroll"] .mcs-item .mcs-caption,
.MagicScroll[data-mode="animation"] .mcs-item .mcs-caption {
  background-color: #31333d;
  background-color: rgba(49, 51, 61, 0.7);
}
.lt-ie9-magic .MagicScroll[data-mode="scroll"] .mcs-item .mcs-caption,
.lt-ie9-magic .MagicScroll[data-mode="animation"] .mcs-item .mcs-caption {
  filter: alpha(opacity=60);
}

/* Carousel & Cover Flow */
.MagicScroll[data-mode="carousel"],
.MagicScroll[data-mode="cover-flow"] {
  background: #4f4f4f;
}
.MagicScroll[data-mode="carousel"] img,
.MagicScroll[data-mode="cover-flow"] img {
  box-shadow: 0px 15px 30px -10px #000;
}
.MagicScroll[data-mode="carousel"] figcaption,
.MagicScroll[data-mode="cover-flow"] figcaption {
  padding: 4px 0 0;
  font-size: 10pt;
  font-family: "Lucida Grande", "Lucida Sans Unicode", "Helvetica Neue", Arial,
    Helvetica, sans-serif;
}

/* Misc */
.MagicScroll.mcs-shadows {
  box-shadow: 0 1px 6px 1px rgba(0, 0, 0, 0.15);
}

.MagicScroll.mcs-border .mcs-item {
  background: #fff;
  border: 1px solid #efefef;
}
.MagicScroll-horizontal.mcs-border .mcs-item {
  margin: 0 3px;
}
.MagicScroll-vertical.mcs-border .mcs-item {
  margin: 3px 0;
}
/*
 * Loader
 */
.mcs-loader {
  pointer-events: none;
  position: absolute;
  z-index: 1000;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}
.mcs-loader .mcs-loader-text {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -10px;
  margin-left: -35px;

  width: 70px;
  height: 20px;
}

.mcs-loader-circles {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 60px;
  height: 60px;
  opacity: 1;
  -webkit-transform: scale(0.6);
  transform: scale(0.6);
}

.mcs-loader-circle {
  position: absolute;
  background-color: #ffffff;
  height: 11px;
  width: 11px;
  border-radius: 50%;

  -webkit-animation-name: MagicCircle;
  -webkit-animation-duration: 0.48s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-direction: linear;

  animation-name: MagicCircle;
  animation-duration: 0.48s;
  animation-iteration-count: infinite;
  animation-direction: linear;
}

.mcs-loader-circle_01 {
  left: 0;
  top: 25px;
  -webkit-animation-delay: 0.18s;
  animation-delay: 0.18s;
}

.mcs-loader-circle_02 {
  left: 7px;
  top: 7px;
  -webkit-animation-delay: 0.24s;
  animation-delay: 0.24s;
}

.mcs-loader-circle_03 {
  left: 25px;
  top: 0;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.mcs-loader-circle_04 {
  right: 7px;
  top: 7px;
  -webkit-animation-delay: 0.36s;
  animation-delay: 0.36s;
}

.mcs-loader-circle_05 {
  right: 0;
  top: 25px;
  -webkit-animation-delay: 0.42s;
  animation-delay: 0.42s;
}

.mcs-loader-circle_06 {
  right: 7px;
  bottom: 7px;
  -webkit-animation-delay: 0.48s;
  animation-delay: 0.48s;
}

.mcs-loader-circle_07 {
  left: 25px;
  bottom: 0;
  -webkit-animation-delay: 0.54s;
  animation-delay: 0.54s;
}

.mcs-loader-circle_08 {
  left: 7px;
  bottom: 7px;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

@-webkit-keyframes MagicCircle {
  0% {
    background-color: #000000;
  }
  100% {
    background-color: #ffffff;
  }
}

@keyframes MagicCircle {
  0% {
    background-color: #000000;
  }
  100% {
    background-color: #ffffff;
  }
}

.mcs-item-loader {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  background-size: cover;
  background-repeat: no-repeat;
  background: url("data:image/gif;base64,")
    no-repeat center center;
}

.MagicScroll.mcs-rounded {
  border: 1px solid #e1e1e1;
  border-radius: 10px;
  overflow: hidden !important;
}
.MagicScroll.mcs-rounded.MagicScroll-arrows-inside {
  overflow: hidden !important;
}
.MagicScroll.mcs-rounded.MagicScroll-arrows-outside {
  overflow: visible !important;
}
.MagicScroll.mcs-rounded .mcs-button-arrow {
  color: rgba(49, 51, 61, 0.6);
  -webkit-transition: color 0.15s linear, background-color 0.15s linear;
  transition: color 0.15s linear, background-color 0.15s linear;
}
.MagicScroll.mcs-rounded.MagicScroll-horizontal .mcs-button-arrow {
  width: 30px;
  height: 100%;
}
.MagicScroll.mcs-rounded.MagicScroll-vertical .mcs-button-arrow {
  width: 100%;
  height: 30px;
}
.MagicScroll.mcs-rounded.MagicScroll-horizontal .mcs-button-arrow-prev {
  left: 0;
  border-radius: 10px 0 0 10px;
}
.MagicScroll.mcs-rounded.MagicScroll-horizontal .mcs-button-arrow-next {
  right: 0;
  border-radius: 0 10px 10px 0;
}

.MagicScroll.mcs-rounded.MagicScroll-vertical .mcs-button-arrow-prev {
  top: 0;
  border-radius: 10px 10px 0 0;
}
.MagicScroll.mcs-rounded.MagicScroll-vertical .mcs-button-arrow-next {
  bottom: 0;
  border-radius: 0 0 10px 10px;
}
.MagicScroll.mcs-rounded .mcs-button-arrow.mcs-disabled {
  opacity: 1 !important;
  color: rgba(49, 51, 61, 0.1);
  text-shadow: none !important;
}

.MagicScroll.mcs-rounded.MagicScroll-arrows-outside .mcs-button-arrow {
  background: #efefef;
  opacity: 1;
  box-shadow: 0 0 1px rgba(153, 153, 153, 0.5);
}

.MagicScroll.mcs-rounded.MagicScroll-arrows-inside
  .mcs-button-arrow:hover:not(.mcs-disabled) {
  background-color: rgba(255, 255, 255, 0.96);
  color: rgba(49, 51, 61, 1);
}
.MagicScroll.mcs-rounded.MagicScroll-arrows-inside .mcs-button-arrow {
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 1px rgba(153, 153, 153, 0.5);
}

.lt-ie10-magic
